@import 'styles/colors';
@import 'styles/media';

.header {
  width: 100%;
  height: 68px;
  border-bottom: 1px solid $gray200;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;

  .logo {
    font-size: 18px;
  }

  .logout {
    position: absolute;
    right: 30px;
    cursor: pointer;
  }

  @media (max-width: $mobile) {
    height: 52px;
  }
}
